g);\n  }\n}\n/* Dropdown Default Transition */\n.vs__fade-enter-active,\n.vs__fade-leave-active {\n  pointer-events: none;\n  transition: opacity 0.15s cubic-bezier(1, 0.5, 0.8, 1);\n}\n\n.vs__fade-enter,\n.vs__fade-leave-to {\n  opacity: 0;\n}\n\n/** Component States */\n/*\n * Disabled\n *\n * When the component is disabled, all interaction\n * should be prevented. Here we modify the bg color,\n * and change the cursor displayed on the interactive\n * components.\n */\n.vs--disabled .vs__dropdown-toggle,\n.vs--disabled .vs__clear,\n.vs--disabled .vs__search,\n.vs--disabled .vs__selected,\n.vs--disabled .vs__open-indicator {\n  cursor: not-allowed;\n  background-color: #f8f8f8;\n}\n\n/*\n *  RTL - Right to Left Support\n *\n *  Because we're using a flexbox layout, the `dir=\"rtl\"`\n *  HTML attribute does most of the work for us by\n *  rearranging the child elements visually.\n */\n.v-select[dir=rtl] .vs__actions {\n  padding: 0 3px 0 6px;\n}\n.v-select[dir=rtl] .vs__clear {\n  margin-left: 6px;\n  margin-right: 0;\n}\n.v-select[dir=rtl] .vs__deselect {\n  margin-left: 0;\n  margin-right: 2px;\n}\n.v-select[dir=rtl] .vs__dropdown-menu {\n  text-align: right;\n}\n\n/**\n    Dropdown Toggle\n\n    The dropdown toggle is the primary wrapper of the component. It\n    has two direct descendants: .vs__selected-options, and .vs__actions.\n\n    .vs__selected-options holds the .vs__selected's as well as the\n    main search input.\n\n    .vs__actions holds the clear button and dropdown toggle.\n */\n.vs__dropdown-toggle {\n  appearance: none;\n  display: flex;\n  padding: 0 0 4px 0;\n  background: none;\n  border: 1px solid rgba(60, 60, 60, 0.26);\n  border-radius: 4px;\n  white-space: normal;\n}\n\n.vs__selected-options {\n  display: flex;\n  flex-basis: 100%;\n  flex-grow: 1;\n  flex-wrap: wrap;\n  padding: 0 2px;\n  position: relative;\n}\n\n.vs__actions {\n  display: flex;\n  align-items: center;\n  padding: 4px 6px 0 3px;\n}\n\n/* Dropdown Toggle States */\n.vs--searchable .vs__dropdown-toggle {\n  cursor: text;\n}\n\n.vs--unsearchable .vs__dropdown-toggle {\n  cursor: pointer;\n}\n\n.vs--open .vs__dropdown-toggle {\n  border-bottom-color: transparent;\n  border-bottom-left-radius: 0;\n  border-bottom-right-radius: 0;\n}\n\n.vs__open-indicator {\n  fill: rgba(60, 60, 60, 0.5);\n  transform: scale(1);\n  transition: transform 150ms cubic-bezier(1, -0.115, 0.975, 0.855);\n  transition-timing-function: cubic-bezier(1, -0.115, 0.975, 0.855);\n}\n\n.vs--open .vs__open-indicator {\n  transform: rotate(180deg) scale(1);\n}\n\n.vs--loading .vs__open-indicator {\n  opacity: 0;\n}\n\n/* Clear Button */\n.vs__clear {\n  fill: rgba(60, 60, 60, 0.5);\n  padding: 0;\n  border: 0;\n  background-color: transparent;\n  cursor: pointer;\n  margin-right: 8px;\n}\n\n/* Dropdown Menu */\n.vs__dropdown-menu {\n  display: block;\n  box-sizing: border-box;\n  position: absolute;\n  top: calc(100% - 1px);\n  left: 0;\n  z-index: 1000;\n  padding: 5px 0;\n  margin: 0;\n  width: 100%;\n  max-height: 350px;\n  min-width: 160px;\n  overflow-y: auto;\n  box-shadow: 0px 3px 6px 0px rgba(0, 0, 0, 0.15);\n  border: 1px solid rgba(60, 60, 60, 0.26);\n  border-top-style: none;\n  border-radius: 0 0 4px 4px;\n  text-align: left;\n  list-style: none;\n  background: #fff;\n}\n\n.vs__no-options {\n  text-align: center;\n}\n\n/* List Items */\n.vs__dropdown-option {\n  line-height: 1.42857143;\n  /* Normalize line height */\n  display: block;\n  padding: 3px 20px;\n  clear: both;\n  color: #333;\n  /* Overrides most CSS frameworks */\n  white-space: nowrap;\n}\n.vs__dropdown-option:hover {\n  cursor: pointer;\n}\n\n.vs__dropdown-option--highlight {\n  background: #5897fb;\n  color: #fff;\n}\n\n.vs__dropdown-option--disabled {\n  background: inherit;\n  color: rgba(60, 60, 60, 0.5);\n}\n.vs__dropdown-option--disabled:hover {\n  cursor: inherit;\n}\n\n/* Selected Tags */\n.vs__selected {\n  display: flex;\n  align-items: center;\n  background-color: #f0f0f0;\n  border: 1px solid rgba(60, 60, 60, 0.26);\n  border-radius: 4px;\n  color: #333;\n  line-height: 1.4;\n  margin: 4px 2px 0px 2px;\n  padding: 0 0.25em;\n  z-index: 0;\n}\n\n.vs__deselect {\n  display: inline-flex;\n  appearance: none;\n  margin-left: 4px;\n  padding: 0;\n  border: 0;\n  cursor: pointer;\n  background: none;\n  fill: rgba(60, 60, 60, 0.5);\n  text-shadow: 0 1px 0 #fff;\n}\n\n/* States */\n.vs--single .vs__selected {\n  background-color: transparent;\n  border-color: transparent;\n}\n.vs--single.vs--open .vs__selected {\n  position: absolute;\n  opacity: 0.4;\n}\n.vs--single.vs--searching .vs__selected {\n  display: none;\n}\n\n/* Search Input */\n/**\n * Super weird bug... If this declaration is grouped\n * below, the cancel button will still appear in chrome.\n * If it's up here on it's own, it'll hide it.\n */\n.vs__search::-webkit-search-cancel-button {\n  display: none;\n}\n\n.vs__search::-webkit-search-decoration,\n.vs__search::-webkit-search-results-button,\n.vs__search::-webkit-search-results-decoration,\n.vs__search::-ms-clear {\n  display: none;\n}\n\n.vs__search,\n.vs__search:focus {\n  appearance: none;\n  line-height: 1.4;\n  font-size: 1em;\n  border: 1px solid transparent;\n  border-left: none;\n  outline: none;\n  margin: 4px 0 0 0;\n  padding: 0 7px;\n  background: none;\n  box-shadow: none;\n  width: 0;\n  max-width: 100%;\n  flex-grow: 1;\n  z-index: 1;\n}\n\n.vs__search::placeholder {\n  color: inherit;\n}\n\n/**\n    States\n */\n.vs--unsearchable .vs__search {\n  opacity: 1;\n}\n.vs--unsearchable:not(.vs--disabled) .vs__search:hover {\n  cursor: pointer;\n}\n\n.vs--single.vs--searching:not(.vs--open):not(.vs--loading) .vs__search {\n  opacity: 0.2;\n}\n\n/* Loading Spinner */\n.vs__spinner {\n  align-self: center;\n  opacity: 0;\n  font-size: 5px;\n  text-indent: -9999em;\n  overflow: hidden;\n  border-top: 0.9em solid rgba(100, 100, 100, 0.1);\n  border-right: 0.9em solid rgba(100, 100, 100, 0.1);\n  border-bottom: 0.9em solid rgba(100, 100, 100, 0.1);\n  border-left: 0.9em solid rgba(60, 60, 60, 0.45);\n  transform: translateZ(0);\n  animation: vSelectSpinner 1.1s infinite linear;\n  transition: opacity 0.1s;\n}\n\n.vs__spinner,\n.vs__spinner:after {\n  border-radius: 50%;\n  width: 5em;\n  height: 5em;\n}\n\n/* Loading Spinner States */\n.vs--loading .vs__spinner {\n  opacity: 1;\n}","$transition-timing-function: cubic-bezier(1.0, 0.5, 0.8, 1.0);\n$transition-duration: .15s;\n\n/* KeyFrames */\n@-webkit-keyframes vSelectSpinner {\n  0% {\n    transform: rotate(0deg);\n  }\n  100% {\n    transform: rotate(360deg);\n  }\n}\n\n@keyframes vSelectSpinner {\n  0% {\n    transform: rotate(0deg);\n  }\n  100% {\n    transform: rotate(360deg);\n  }\n}\n\n/* Dropdown Default Transition */\n.vs__fade-enter-active,\n.vs__fade-leave-active {\n  pointer-events: none;\n  transition: opacity $transition-duration $transition-timing-function;\n}\n.vs__fade-enter,\n.vs__fade-leave-to {\n  opacity: 0;\n}\n","/** Component States */\n\n/*\n * Disabled\n *\n * When the component is disabled, all interaction\n * should be prevented. Here we modify the bg color,\n * and change the cursor displayed on the interactive\n * components.\n */\n\n$disabled-bg: $vs-state-disabled-bg;\n$disabled-color: $vs-state-disabled-color;\n$disabled-cursor: $vs-state-disabled-cursor;\n\n.vs--disabled {\n  .vs__dropdown-toggle,\n  .vs__clear,\n  .vs__search,\n  .vs__selected,\n  .vs__open-indicator {\n    cursor: $disabled-cursor;\n    background-color: $disabled-bg;\n  }\n}\n\n/*\n *  RTL - Right to Left Support\n *\n *  Because we're using a flexbox layout, the `dir=\"rtl\"`\n *  HTML attribute does most of the work for us by\n *  rearranging the child elements visually.\n */\n\n.v-select[dir=\"rtl\"] {\n  .vs__actions {\n    padding: 0 3px 0 6px;\n  }\n\n  .vs__clear {\n    margin-left: 6px;\n    margin-right: 0;\n  }\n\n  .vs__deselect {\n    margin-left: 0;\n    margin-right: 2px;\n  }\n\n  .vs__dropdown-menu {\n    text-align: right;\n  }\n}\n","$vs-colors: (\n        lightest: rgba(60, 60, 60, 0.26),\n        light: rgba(60, 60, 60, 0.5),\n        dark: #333,\n        darkest: rgba(0, 0, 0, .15),\n) !default;\n\n//  Global Component Variables\n$vs-component-line-height: 1.4 !default;\n$vs-component-placeholder-color: inherit !default;\n\n//  Active State\n$vs-state-active-bg: #5897fb !default;\n$vs-state-active-color: #fff !default;\n\n//  Disabled State\n$vs-state-disabled-bg: rgb(248, 248, 248) !default;\n$vs-state-disabled-color: map_get($vs-colors, 'light') !default;\n$vs-state-disabled-controls-color: map_get($vs-colors, 'light') !default;\n$vs-state-disabled-cursor: not-allowed !default;\n\n//  Borders\n$vs-border-width: 1px !default;\n$vs-border-style: solid !default;\n$vs-border-radius: 4px !default;\n$vs-border-color: map_get($vs-colors, 'lightest') !default;\n\n//  Component Controls: Clear, Open Indicator\n$vs-controls-color: map_get($vs-colors, 'light') !default;\n$vs-controls-size: 1 !default;\n$vs-controls-deselect-text-shadow: 0 1px 0 #fff;\n\n//  Selected\n$vs-selected-bg: #f0f0f0 !default;\n$vs-selected-border-color: $vs-border-color !default;\n$vs-selected-border-style: $vs-border-style !default;\n$vs-selected-border-width: $vs-border-width !default;\n\n//  Dropdown\n$vs-dropdown-z-index: 1000 !default;\n$vs-dropdown-min-width: 160px !default;\n$vs-dropdown-max-height: 350px !default;\n$vs-dropdown-box-shadow: 0px 3px 6px 0px map_get($vs-colors, 'darkest') !default;\n$vs-dropdown-bg: #fff !default;\n","/**\n    Dropdown Toggle\n\n    The dropdown toggle is the primary wrapper of the component. It\n    has two direct descendants: .vs__selected-options, and .vs__actions.\n\n    .vs__selected-options holds the .vs__selected's as well as the\n    main search input.\n\n    .vs__actions holds the clear button and dropdown toggle.\n */\n\n$border-width: $vs-border-width;\n$border-style: $vs-border-style;\n$border-color: $vs-border-color;\n$border-radius: $vs-border-radius;\n\n.vs__dropdown-toggle {\n  appearance: none;\n  display: flex;\n  padding: 0 0 4px 0;\n  background: none;\n  border: $border-width $border-style $border-color;\n  border-radius: $border-radius;\n  white-space: normal;\n}\n\n.vs__selected-options {\n  display: flex;\n  flex-basis: 100%;\n  flex-grow: 1;\n  flex-wrap: wrap;\n  padding: 0 2px;\n  position: relative;\n}\n\n.vs__actions {\n  display: flex;\n  align-items: center;\n  padding: 4px 6px 0 3px;\n}\n\n/* Dropdown Toggle States */\n.vs--searchable .vs__dropdown-toggle {\n  cursor: text;\n}\n.vs--unsearchable .vs__dropdown-toggle {\n  cursor: pointer;\n}\n.vs--open .vs__dropdown-toggle {\n  border-bottom-color: transparent;\n  border-bottom-left-radius: 0;\n  border-bottom-right-radius: 0;\n}\n","//  Open Indicator\n\n//  The open indicator appears as a down facing\n//  caret on the right side of the select.\n\n$transition-timing-function: cubic-bezier(1.000, -0.115, 0.975, 0.855);\n$transition-duration: 150ms;\n\n$open-indicator-color: $vs-controls-color;\n$open-indicator-size: $vs-controls-size;\n\n.vs__open-indicator {\n  fill: $open-indicator-color;\n  transform: scale($open-indicator-size);\n  transition: transform $transition-duration $transition-timing-function;\n  transition-timing-function: $transition-timing-function;\n}\n\n//  Open State\n\n.vs--open .vs__open-indicator {\n  transform: rotate(180deg) scale($open-indicator-size);\n}\n\n//  Loading State\n\n.vs--loading .vs__open-indicator {\n  opacity: 0;\n}\n","/* Clear Button */\n\n.vs__clear {\n  fill: $vs-controls-color;\n  padding: 0;\n  border: 0;\n  background-color: transparent;\n  cursor: pointer;\n  margin-right: 8px;\n}\n","/* Dropdown Menu */\n\n$border-width: $vs-border-width;\n$border-style: solid;\n$border-color: $vs-border-color;\n$border-radius: $vs-border-radius;\n$box-shadow: $vs-dropdown-box-shadow;\n\n$bg-color: $vs-dropdown-bg;\n$z-index: $vs-dropdown-z-index;\n$min-width: $vs-dropdown-min-width;\n$max-height: $vs-dropdown-max-height