\n    position: absolute;\n    opacity: .4;\n  }\n  &.vs--searching .vs__selected {\n    display: none;\n  }\n}\n","/* Search Input */\n\n$line-height: $vs-component-line-height;\n$font-size: 1em;\n\n/**\n * Super weird bug... If this declaration is grouped\n * below, the cancel button will still appear in chrome.\n * If it's up here on it's own, it'll hide it.\n */\n.vs__search::-webkit-search-cancel-button {\n  display: none;\n}\n\n.vs__search::-webkit-search-decoration,\n.vs__search::-webkit-search-results-button,\n.vs__search::-webkit-search-results-decoration,\n.vs__search::-ms-clear {\n  display: none;\n}\n\n.vs__search,\n.vs__search:focus {\n  appearance: none;\n  line-height: $line-height;\n  font-size: $font-size;\n  border: 1px solid transparent;\n  border-left: none;\n  outline: none;\n  margin: 4px 0 0 0;\n  padding: 0 7px;\n  background: none;\n  box-shadow: none;\n  width: 0;\n  max-width: 100%;\n  flex-grow: 1;\n  z-index: 1;\n}\n\n.vs__search::placeholder {\n  color: $vs-component-placeholder-color;\n}\n\n/**\n    States\n */\n\n// Unsearchable\n.vs--unsearchable {\n  .vs__search {\n    opacity: 1;\n  }\n  &:not(.vs--disabled) .vs__search:hover {\n    cursor: pointer;\n  }\n}\n// Single, when searching but not loading or open\n.vs--single.vs--searching:not(.vs--open):not(.vs--loading) {\n    .vs__search {\n      opacity: .2;\n    }\n}\n","/* Loading Spinner */\n.vs__spinner {\n  align-self: center;\n  opacity: 0;\n  font-size: 5px;\n  text-indent: -9999em;\n  overflow: hidden;\n  border-top: .9em solid rgba(100, 100, 100, .1);\n  border-right: .9em solid rgba(100, 100, 100, .1);\n  border-bottom: .9em solid rgba(100, 100, 100, .1);\n  border-left: .9em solid rgba(60, 60, 60, .45);\n  transform: translateZ(0);\n  animation: vSelectSpinner 1.1s infinite linear;\n  transition: opacity .1s;\n}\n.vs__spinner,\n.vs__spinner:after {\n  border-radius: 50%;\n  width: 5em;\n  height: 5em;\n}\n\n/* Loading Spinner States */\n.vs--loading .vs__spinner {\n  opacity: 1;\n}\n"],"sourceRoot":""}]);
